<template>
  <el-row>
    <el-col :span="24">
      <el-menu
        mode="horizontal"
        :defaultActive="$route.path"
        :unique-opened="true"
        router>
        <el-menu-item class="titleStyle">系统管理平台</el-menu-item>
        <template v-for="(menu, index) in menusTree">
          <el-submenu :key="menu.resourceId"
                      v-if="menu.children !== undefined"
                      :index="index.toString()">
            <span slot="title" style="color: aliceblue">{{menu.resourceName}}</span>
            <el-menu-item v-for="childMenu in menu.children" :key="childMenu.resourceId"
                          :index="childMenu.resourceUrl"
                          >
              <el-submenu v-if="childMenu.children !== undefined" :key="menu.resourceId">
                <template slot="title">{{childMenu.resourceName}}</template>
                <el-menu-item v-for="childChildMenu in childMenu.children" :key="childChildMenu.resourceId"
                              :index="childChildMenu.resourceUrl"
                              >
                  <span slot="title">{{childChildMenu.resourceName}}</span>
                </el-menu-item>
              </el-submenu>
              <el-menu-item :key="childMenu.resourceId"
                            v-if="childMenu.children === undefined"
                            :index="childMenu.resourceUrl"
                            >
                <span slot="title">{{childMenu.resourceName}}</span>
              </el-menu-item>
            </el-menu-item>
          </el-submenu>
          <el-menu-item :key="menu.resourceId"
                        v-if="menu.children === undefined"
                        :index="menu.resourceUrl">
            <span slot="title">{{menu.resourceName}}</span>
          </el-menu-item>
        </template>

        <el-submenu style="float: right" index="_userInfo" >
          <template slot="title">
            <span style="color:aliceblue">
            欢迎你！{{user.username}}
            </span>
          </template>
          <el-menu-item index="/logout" @click.native="logout">
            <i class="el-icon-setting"></i>
            <span slot="title">登出</span>
          </el-menu-item>
          <el-menu-item  @click.native="changePrivilege">
            <i class="el-icon-user"></i>
            <span slot="title">切换数据权限</span>
          </el-menu-item>
        </el-submenu>


      </el-menu>
    </el-col>
  </el-row>
</template>
<script>
  import Constants from '@common/constants/constants';
  import {toSSO} from '@common/util';
  export default {
    data() {
      return {
        user: JSON.parse(localStorage.getItem(Constants.USER_KEY))
      };
    },
    mounted() {
      const index = this.$store.getters['auth/menus'].filter((m)=>m.resourceUrl).map((m)=>m.resourceUrl);
      this.$router.push(index.indexOf('/index')!==-1?'/index':index[0]);
    },
    computed: {
      menusTree() {
        return this.$store.getters['auth/menusTree'];
      },
      defaultActive() {
        console.log(this.$route.path.replace('/', ''));
        return this.$route.path;
      }
    },
    methods: {
      logout() {
        this.$store.dispatch('auth/logout');
        toSSO();
      },
      changePrivilege() {
        let privilegeList = JSON.parse(localStorage.getItem('privilegeList'));
        if (privilegeList && privilegeList.length > 1) {
          this.$store.commit('privilege/setDataId', '');
          this.$router.push({path: '/privilege'});
        } else {
          this.$message({
            showClose: true,
            message: `当前有${privilegeList.length}条数据权限，无法切换`,
            type: 'error'
          });
        }
      }
    }
  };
</script>
<style scoped>
.titleStyle {
  font-size: 24px;
  font-weight: 550px;
  color: aliceblue;
  pointer-events:none;
}
.el-menu {
  background-image: url("~@assets/images/header-background.png");
  background-size: 100%;
  width: 100%;

}
.el-menu :hover{
  background-color: rgb(50, 128, 230) !important;
}

.el-menu--horizontal>.el-submenu .el-submenu__title {
    height: 60px;
    line-height: 60px;
    border-bottom: 2px solid transparent;
    color: aliceblue;
}
.el-menu--horizontal>.el-menu-item.is-active {
    border-bottom: 2px solid #409EFF;
    color: aliceblue;
}
.el-menu--horizontal>.el-menu-item {
    color: aliceblue;
}
.el-menu--horizontal .el-menu-item:not(.is-disabled):focus, .el-menu--horizontal .el-menu-item:not(.is-disabled):hover {
    outline: 0;
    color: aliceblue;
}
.el-menu-item :hover {

  pointer-events:none;
}
</style>
<style>
.el-header {
    padding:  0px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -ms-flex-negative: 0;
    flex-shrink: 0;
}
html, body, #app {
    height: 100%;
    overflow-y: hidden;
    margin: 0px
}
.el-main {
    padding: 10px;
}
.el-menu-item :hover {
  color: darkgray !important;
  pointer-events:none !important;
}

</style>

